*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
*::after,*::before{
    box-sizing: border-box;
}
body{
    background-color: #fafad2;
    min-height: 100vh;
}
.wrapper{
    width: 400px;
    height: 700px;
    position: relative;
    left: 50%;
    top: 100px;
    margin-left: -200px;
}
.head{
    width: 400px;
    height: 400px;
}
.head .face{
    width: 360px;
    height: 340px;
    position: relative;
    top: 0;
    left: 50%;
    margin-left: -180px;
    background: #1e90ff;
    border: 2px solid #000;
    border-radius: 50%;
}
.head .face .face1{
	position: absolute;
	bottom: 0;
	left: 30px;
	width: 300px;
	height: 250px;
	background-color: #FFF;
	border: 2px solid #000;
	border-radius: 50% 50% 45% 45%;
}
.head .eye{
    width: 90px;
    height: 110px;
    border: 2px solid #000;
    background: #fff;
    position: absolute;
    left: 50%;
    top:-50px;
    margin-left: -45px;
    border-radius: 46%;
}
.head .eye::before{
    content: '';
    position: absolute;
    display: block;
    width: 20px;
    height: 20px;
    background: #000;
    border-radius: 50%;
    top: 55px;
    animation: wave 3s infinite linear;
}
.head .eye.left{
    transform: translateX(-45px);
}
.head .eye.left::before{
    left: 60px;
}
.head .eye.eye.right::before{
    right: 60px;
}
.head .eye.right{
    transform: translateX(45px);
}
.head .nose{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    position: absolute;
    background-color: #FF0000;
	border: 2px solid #000;
    top: 40px;
    left: 50%;
    margin-left: -25px;
}
.head .nose::before{
    content: '';
    position: absolute;
    display: block;
    width: 15px;
    height: 15px;
    background-color: #FFF;
	border-radius: 50%;
	box-shadow: 0px 0px 2px 2px rgba(255, 255, 255, .4);
    top: 12px;
    left: 22px;
}
.head .nose2{
    height: 110px;
	width: 2px;
	position: absolute;
    border:1px solid #000;
    top: 90px;
    left: 50%;
    margin-left: -1px;
}
.head .mouth{
    position: absolute;
    width: 240px;
    height: 200px;
    left: 50%;
    top: 1px;
    margin-left: -120px;
    border-bottom: 2px solid #000;
    border-radius: 0 0 45% 45%;
}
.head .whisker{
    position: absolute;
    width: 100px;
    height: 100px;
    left: 50%;
    top: 60px;
    margin-left: -50px;
}
.head .whisker.left{
    transform: translateX(-80px);
}
.head .whisker.right{
    transform: translateX(80px);
}
.head .whisker span{
    position: absolute;
    width: 80px;
    height: 60px;
    top: 20px;
    left: 10px;
    transform-origin: 0 1;
    border-top: 2px solid black;
    /* border: 1px solid black; */
}
.head .whisker.left span:nth-child(1){
    transform: rotate(20deg);
    left: -5px;
}
.head .whisker.right span:nth-child(1){
    transform: rotate(-20deg);
    left: 20px;
}
.head .whisker.left span:nth-child(2){
    left: 2px;
    top: 45px;
}
.head .whisker.right span:nth-child(2){
    left: 12px;
    top: 45px;
}
.head .whisker.left span:nth-child(3){
    transform: rotate(-20deg);
    left: 14px;
    top: 68px;
}
.head .whisker.right span:nth-child(3){
    transform: rotate(20deg);
    left: 0px;
    top: 68px;
}
.head .neckBox{
    position: absolute;
    width: 220px;
    height: 42px;
    border: 2px solid #000;
    background-color: #FF0000;
    border-radius: 20px;
    left: 50%;
    margin-left: -110px;
    top: 315px;
    z-index: 10;
}
.head .neckBox .bell{
    position: absolute;
    width: 50px;
    height: 50px;
    background-color: #FFFF00;
	border: 2px solid #000;
	border-radius: 50%;
    left: 50%;
    margin-left: -25px;
    top: 12px;
    overflow: hidden;
}
.head .neckBox .bell:hover{
    cursor: pointer;
    animation: huang 100ms infinite linear;
}
.head .neckBox .bell::before{
    content: '';
    position: absolute;
    display: block;
    width: 15px;
    height: 10px;
    background-color: #000;
	border-radius: 50%;
    top: 20px;
    left: 15px;
}
.head .neckBox .bell .line1{
    position: absolute;
    width: 50px;
    height: 8px;
    border: 2px solid #000;
    left:-2px;
    top:10px;
}
.head .neckBox .bell .line2{
    position: absolute;
    width: 50px;
    height: 20px;
    border-left: 2px solid #000;
    left:22px;
    top:28px;
}
.body{
    position: absolute;
    width: 220px;
    height: 240px;
    background: #1e90ff;
    border-left:2px solid #000;
    border-right:2px solid #000;
    left: 50%;
    margin-left: -110px;
    top: 350px;
}
.body .yuan{
    position: absolute;
    border-radius: 50%;
    background: #fff;
    border:2px solid #000;
    left: 50%;
    width: 160px;
    height: 160px;
    margin-left: -80px;
    top: -30px;
    z-index: 2;
}
.body .yuan .yuan2{
    position: absolute;
    background: #fff;
    border:2px solid #000;
    width: 100px;
    height: 50px;
    left: 50%;
    margin-left: -50px;
    border-radius: 0 0 120px 120px;
    top: 80px;
}
.body .foot{
    position: absolute;
    width: 130px;
    height: 35px;
    border: 2px solid #000;
    background: #fff;
    border-radius: 35px;
    left: 50%;
    margin-left: -65px;
    top: 215px;
    z-index: 1;
}
.body .foot.left{
    transform: translateX(-70px);
}
.body .foot.right{
    transform: translateX(70px);
}
.body .yuan3{
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 50% 50% 0 0;
    border-left:2px solid #000;
    border-right:2px solid #000;
    border-top:2px solid #000;
    background: #fafad2;
    left: 50%;
    margin-left: -25px;
    top: 190px;
}
.hand{
    position: absolute;
    width: 140px;
    height: 50px;
    border:2px solid #000;
    background: #1e90ff;
    left: 50%;
    margin-left: -70px;
    top: 385px;
    transform-origin: 0 0.5;
}
.hand::before{
    content: '';
    position: absolute;
    display: block;
    width:60px;
    height: 60px;
    background-color: #FFF;
    border:2px solid #000;
    border-radius: 50%;
    top: -9px;
    z-index: 5;
}
.hand.left{
    transform: rotate(-60deg);
    left:76px;
    border-right: none;
    border-bottom: none;
}
.hand.left::before{
    left: -22px;
}
.hand.right{
    transform: rotate(60deg);
    left:322px;
    border-left: none;
    border-bottom: none;
}
.hand.right::before{
    left: 100px;
}
.hand .line4{
    position: absolute;
    border-top: 2px solid #000;
    width: 50px;
    height: 2px;
    z-index: 3;
    top:47px;
}
.hand.left .line4{
    left: 5px;
}
.hand.right .line4{
    left: 87px;
}
.heart{
    position: absolute;
    left:315px;
    top: 195px;
    z-index: 100;
    width:80px;
    height:80px;
    background-color:#cc2a5d;
    margin:200px auto;
    transform: rotate(45deg);
    animation: heart 3s ease-in infinite;
}
.heart::before,
.heart::after{
    content:'';
    width:100%;
    height:100%;
    background-color:#cc2a5d;
    border-radius: 50%;
}
.heart::before{
    position: absolute;
    top:0;
    left:0;
    transform: translateX(-50%);
}
.heart::after{
    position: absolute;
    top:0;
    left:0;
    transform: translateY(-50%);
}
@keyframes heart{
    0%{
        transform: scale(0.8) rotate(45deg);
        opacity: 0.8;
    }
    50%{
        transform: scale(1) rotate(45deg);
        opacity: 1;
    }
    100%{
        transform: scale(0.8) rotate(45deg);
        opacity: 0.8;
    }
}

@keyframes wave{
    0%{
        transform: translate(0px,0px);
    }
    25%{
        transform: translate(2px,-3px);
    }
    50%{
        transform: translate(4px,4px);
    }
    75%{
        transform: translate(-3px,2px);
    }
    100%{
        transform: translate(0px,0px);
    }
}
@keyframes huang{
    0%{
        transform: rotate(0deg);
    }
    33%{
        transform: rotate(5deg);
    }
    66%{
        transform: rotate(-5deg);
    }
    100%{
        transform: rotate(0deg);
    }
}